*,*::after,*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html {
    scroll-behavior: smooth;
  }

  body{
    background-image: linear-gradient(to right top, #259eaf, #64aec7, #93bed9, #bbcfe6, #dde2f1, #dee2f0, #dfe2ef, #e0e2ee, #c5cee1, #a6bbd5, #84a9c7, #5d98b7);
    font-family: "Sofia", sans-serif;
  }

  a{
    text-decoration: 'none'
  }
  